export default {
	components: { uniGrid },
	data() {
		return {
			article: {
				aId: 0,
				uId: 0,
				title: '',
				content: '',
				avatar: '',
				nickname: '',
				createTime: ''
			},
			comments: [],
			content: '',
			userId: uni.getStorageSync('login_key').userId,
			followed: false,
			liked: false
		};
	},
	onLoad: function(option) {
		//option为object类型，会序列化上个页面传递的参数
		this.article.aId = option.aId;
	},
	onShow: function() {
		this.getArticle();
	},
	onPullDownRefresh: function() {
		this.getArticle();
	},
	methods: {
		goBack: function() {
			uni.navigateBack({});
		},
		tocomment: function() {
			uni.createSelectorQuery()
				.select('#comment_container')
				.boundingClientRect(function(rect) {
					// 使页面滚动到底部
					uni.pageScrollTo({
						scrollTop: rect.bottom
					});
				})
				.exec();
		},
		getArticle: function() {
			var _this = this;
			uni.request({
				url: this.apiServer + '/article/' + this.article.aId,
				method: 'GET',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					userId: this.userId
				},
				success: res => {
					// console.log(res.data.data.article);
					_this.article.aId = res.data.data.article.id;
					_this.article.uId = res.data.data.article.uid;
					_this.article.title = res.data.data.article.title;
					_this.article.content = res.data.data.article.content;
					_this.article.nickname = res.data.data.article.nickname;
					_this.article.avatar = res.data.data.article.avatar;
					_this.article.createTime = res.data.data.article.createTime;
					_this.comments = res.data.data.comments;
					_this.article.createTime = this.handleTime(_this.article.createTime);
					for (var i = 0; i < _this.comments.length; i++) {
						_this.comments[i].commentTime = this.handleTime(_this.comments[i].commentTime);
					}
					if (res.data.data.followed === '已关注') {
						_this.followed = true;
					}
					if (res.data.data.liked === '喜欢') {
						_this.liked = true;
					}
				},
				complete: function() {
					uni.stopPullDownRefresh();
				}
			});
		},
		handleTime: function(date) {
			var d = new Date(date);
			var year = d.getFullYear();
			var month = d.getMonth() + 1;
			var day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate();
			var hour = d.getHours() < 10 ? '0' + d.getHours() : '' + d.getHours();
			var minutes = d.getMinutes() < 10 ? '0' + d.getMinutes() : '' + d.getMinutes();
			var seconds = d.getSeconds() < 10 ? '0' + d.getSeconds() : '' + d.getSeconds();
			return year + '-' + month + '-' + day + ' ' + hour + ':' + minutes + ':' + seconds;
		},
		send: function() {
			var _this=this;
			console.log('评论人编号：' + this.userId + ',文章编号：' + this.article.aId + '，评论内容：' + this.content);
			uni.request({
				url: this.apiServer + '/comment/add',
				method: 'POST',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					aId: this.article.aId,
					uId: this.userId,
					content: this.content
				},
				success: res => {
					if (res.data.code === 0) {
						uni.showToast({
							title: '简阅钻+5'
						});
						this.getArticle();
						this.content = '';
					}
				},
				complete:function(){
					var newScore=uni.getStorageSync('login_key').score+5;
					uni.request({
						url: 'http://47.101.34.195:8080/api/user/score',
						method: 'post',
						header: { 'content-type': 'application/x-www-form-urlencoded' },
						data: {
							userId: _this.userId,
							score: newScore
						},
						success: resde => {
							console.log(resde.data);
							this.promptVisible = false;
							uni.request({
								url: 'http://47.101.34.195:8080/api/user/' + uni.getStorageSync('login_key').userId,
								method: 'GET',
								data: {
									userId: _this.userId
								},
								header: {
									'content-type': 'application/json'
								},
								success: sign => {
									if (sign.data.code == 0) {
										//将用户数据记录在本地存储
										uni.setStorageSync('login_key', {
											userId: sign.data.data.id,
											nickname: sign.data.data.nickname,
											avatar: sign.data.data.avatar,
											token: sign.data.data.token,
											score:sign.data.data.score,
											mobile: sign.data.data.mobile,
											login: true
										});
									}
								}
							});
						}
					});
				}
			});
		},
		likeArticke: function() {
			var _this=this;
			uni.request({
				url: this.apiServer + '/like/add',
				method: 'POST',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					aId: this.article.aId,
					uId: this.userId
				},
				success: res => {
					uni.showToast({
						title:'简阅钻+5'
					})
					this.liked = true;
				},
				complete:function(){
					var newScore=uni.getStorageSync('login_key').score+5;
					uni.request({
						url: 'http://47.101.34.195:8080/api/user/score',
						method: 'post',
						header: { 'content-type': 'application/x-www-form-urlencoded' },
						data: {
							userId: _this.userId,
							score: newScore
						},
						success: resd => {
							console.log(resd.data);
							this.promptVisible = false;
							uni.request({
								url: 'http://47.101.34.195:8080/api/user/' + uni.getStorageSync('login_key').userId,
								method: 'GET',
								data: {
									userId: _this.userId
								},
								header: {
									'content-type': 'application/json'
								},
								success: res => {
									if (res.data.code == 0) {
										//将用户数据记录在本地存储
										uni.setStorageSync('login_key', {
											userId: res.data.data.id,
											nickname: res.data.data.nickname,
											avatar: res.data.data.avatar,
											token: res.data.data.token,
											score:res.data.data.score,
											mobile: res.data.data.mobile,
											login: true
										});
									}
								}
							});
						}
					});
				}
			});
		},
		cancelLike: function() {
			uni.request({
				url: this.apiServer + '/like/cancel',
				method: 'POST',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					aId: this.article.aId,
					uId: this.userId
				},
				success: res => {
					this.liked = false;
				}
			});
		},
		follow: function() {
			uni.request({
				url: this.apiServer + '/follow/add',
				method: 'POST',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					fromUId: this.userId,
					toUId: this.article.uId
				},
				success: res => {
					if (res.data.code === 0) {
						uni.showToast({
							title: '关注成功'
						});
						this.followed = true;
					}
				}
			});
		},
		cancelFollow: function() {
			uni.request({
				url: this.apiServer + '/follow/cancel',
				method: 'POST',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					fromUId: this.userId,
					toUId: this.article.uId
				},
				success: res => {
					if (res.data.code === 0) {
						uni.showToast({
							title: '已取消关注'
						});
						this.followed = false;
					}
				}
			});
		}
	}
};
